  padding: 1rem;
}
.panel-header {
  color: #009688;
  font-size: 1.5rem;
}

/* When the screen is below 700px reduce columns on display*/
@media only screen and (max-width: 700px) {
  .grid-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
  }
  .grid-header {
    color: #009688;
    grid-column: 1 / span 1;
  }
  .panel-header {
    grid-column: 1 / span 1;
  }
  .grid-image {
    grid-column: 1 / span 1;
  }
}

